import React, { Component } from "react";
import firebase from "../../firebase";
import { Card } from "semantic-ui-react";
import MyCard from "./mycard";
class Supporter extends Component {
  state = { deliveries: [] };

  componentDidMount() {
    firebase
      .firestore()
      .collection("delivery")
      .limit(3)
      .get()
      .then(snapshot => {
        const deliveries = [];
        snapshot.forEach(doc => {
          const { from, to, price, quantity, total } = doc.data();
          deliveries.push({
            from,
            to,
            price,
            quantity,
            total
          });
        });
        this.setState({ deliveries });
      });
  }

  render() {
    return (
      <div>
        <Card style={{ width: "100%" }}>
          <Card.Content>
            <Card.Header>Recent Deliveries</Card.Header>
          </Card.Content>
          {this.state.deliveries.map(delivery => {
            return <MyCard delivery={delivery} />;
          })}
        </Card>
      </div>
    );
  }
}

export default Supporter;
